import Vue from 'vue';
//配置路由
import VueRouter from 'vue-router'
Vue.use(VueRouter);
    // 解决多次点击左侧菜单报错问题
    const VueRouterPush = VueRouter.prototype.push
    VueRouter.prototype.push = function push (to) {
    return VueRouterPush.call(this, to).catch(err => err)
    }
//1.创建组件
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
import beifen from '@/views/modules/databaseBackup/beifen'
import huanyuan from '@/views/modules/databaseBackup/huanyuan'

     import users from '@/views/modules/users/list'
    import banji from '@/views/modules/banji/list'
    import chengji from '@/views/modules/chengji/list'
    import dictionary from '@/views/modules/dictionary/list'
    import gonggao from '@/views/modules/gonggao/list'
    import laoshi from '@/views/modules/laoshi/list'
    import xuesheng from '@/views/modules/xuesheng/list'
    import xueshengKaoqin from '@/views/modules/xueshengKaoqin/list'
    import xueshengKaoqinList from '@/views/modules/xueshengKaoqinList/list'
    import xueshengqingjia from '@/views/modules/xueshengqingjia/list'
    import dictionaryChengji from '@/views/modules/dictionaryChengji/list'
    import dictionaryGonggao from '@/views/modules/dictionaryGonggao/list'
    import dictionaryJinyong from '@/views/modules/dictionaryJinyong/list'
    import dictionaryKemu from '@/views/modules/dictionaryKemu/list'
    import dictionaryNianji from '@/views/modules/dictionaryNianji/list'
    import dictionarySex from '@/views/modules/dictionarySex/list'
    import dictionaryXueshengKaoqin from '@/views/modules/dictionaryXueshengKaoqin/list'
    import dictionaryXueshengKaoqinList from '@/views/modules/dictionaryXueshengKaoqinList/list'
    import dictionaryXueshengqingjia from '@/views/modules/dictionaryXueshengqingjia/list'
    import dictionaryXueshengqingjiaYesno from '@/views/modules/dictionaryXueshengqingjiaYesno/list'





//2.配置路由   注意：名字
const routes = [{
    path: '/index',
    name: '首页',
    component: Index,
    children: [{
      // 这里不设置值，是把main作为默认页面
      path: '/',
      name: '首页',
      component: Home,
      meta: {icon:'', title:'center'}
    }, {
      path: '/updatePassword',
      name: '修改密码',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '支付',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '个人信息',
      component: center,
      meta: {icon:'', title:'center'}
    }, {
        path: '/huanyuan',
        name: '数据还原',
        component: huanyuan
    }, {
        path: '/beifen',
        name: '数据备份',
        component: beifen
    }, {
        path: '/users',
        name: '管理信息',
        component: users
    }
    ,{
        path: '/dictionaryChengji',
        name: '成绩类型',
        component: dictionaryChengji
    }
    ,{
        path: '/dictionaryGonggao',
        name: '公告类型',
        component: dictionaryGonggao
    }
    ,{
        path: '/dictionaryJinyong',
        name: '账户状态',
        component: dictionaryJinyong
    }
    ,{
        path: '/dictionaryKemu',
        name: '科目',
        component: dictionaryKemu
    }
    ,{
        path: '/dictionaryNianji',
        name: '年级',
        component: dictionaryNianji
    }
    ,{
        path: '/dictionarySex',
        name: '性别类型',
        component: dictionarySex
    }
    ,{
        path: '/dictionaryXueshengKaoqin',
        name: '学生考勤类型',
        component: dictionaryXueshengKaoqin
    }
    ,{
        path: '/dictionaryXueshengKaoqinList',
        name: '打卡状态',
        component: dictionaryXueshengKaoqinList
    }
    ,{
        path: '/dictionaryXueshengqingjia',
        name: '请假类型',
        component: dictionaryXueshengqingjia
    }
    ,{
        path: '/dictionaryXueshengqingjiaYesno',
        name: '申请状态',
        component: dictionaryXueshengqingjiaYesno
    }


    ,{
        path: '/banji',
        name: '班级',
        component: banji
      }
    ,{
        path: '/chengji',
        name: '成绩',
        component: chengji
      }
    ,{
        path: '/dictionary',
        name: '字典',
        component: dictionary
      }
    ,{
        path: '/gonggao',
        name: '公告',
        component: gonggao
      }
    ,{
        path: '/laoshi',
        name: '老师',
        component: laoshi
      }
    ,{
        path: '/xuesheng',
        name: '学生',
        component: xuesheng
      }
    ,{
        path: '/xueshengKaoqin',
        name: '学生考勤',
        component: xueshengKaoqin
      }
    ,{
        path: '/xueshengKaoqinList',
        name: '学生考勤详情',
        component: xueshengKaoqinList
      }
    ,{
        path: '/xueshengqingjia',
        name: '学生请假',
        component: xueshengqingjia
      }


    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '/',
    name: '首页',
    redirect: '/index'
  }, /*默认跳转路由*/
  {
    path: '*',
    component: NotFound
  }
]
//3.实例化VueRouter  注意：名字
const router = new VueRouter({
  mode: 'hash',
  /*hash模式改为history*/
  routes // （缩写）相当于 routes: routes
})

export default router;
